.card_image_info {
	position: relative;
	/* height: 222px; */
	/* padding: 15px 12px 12px 12px; */
}
.cardList__card_image_info_white {
	box-shadow: 0px 1px 6px 0px #0000001A;
	border-radius: 8px;
	overflow: hidden;
}
.card_image {
	width: 100%;
	top: 0;
	left: 0;
}
.card_details {
	display: flex;
	flex-direction: column;
	height: calc(100% - 15px);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding: 0 15px;
}
.secret-wrapper iframe {
	width: 100%;
	height: 100%;
}
.cancel_card {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0 16px 32px 16px;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 0;
	background: rgba(0, 0, 0, 0.6);
	padding-bottom: 0;
}
.sent_card:hover .cancel_card{
	opacity: 1;
}
.sent_card .cancel_card button {
	color: #FFFFFF;
	background-color: #F56C6C;
	border-color: #F56C6C;
	width: auto;
}
.is_cancelled {
	opacity: 0.5;
	cursor: default;
	pointer-events: none;
}

