
.cardDetails__card_image_info {
	position: relative;
	height: 237px;
	padding: 15px 12px 12px 12px;
	margin: 16px 0;
}

.cardDetails__card_image {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.cardDetails__card_details {
	position: relative;
	display: flex;
	flex-direction: column;
	height: 100%;
}

.pointer {
	cursor: pointer;
}

.wallet {
	height: 26.74px;
	max-width: 167px;
}

.cardDetails__action_in_list {
	border: 1px solid var(--border-color-1);
	border-radius: 4px;
	display: flex;
	align-items: center;
	padding: 22px 16px;
	margin: 0 0 12px 0;
	background: var(--bg-white);
}

.iconCopy {
	cursor: pointer;
	margin-left: auto;
	width: 30px;
	height: 25px;
	overflow: hidden;
}
.cardNumber {
	max-width: 200px;
	height: 25px;
	margin-left: 6px;
	transform: translateY(2px); 
}

.cardDetails__cardNo {
	margin-top: 24px;
}
